.container {
  display: flex;
  align-items: center;
  justify-content: center;

  width: $width-dial;
  height: $height-dial;

  border: 1px solid #000;
  background-image: url(../../src/img/4ibK6kjxT.png);
  background-position: center;
  background-size: cover;

  &__arrows {
    position: relative;

    height: $center-size;
    width: $center-size;
  }
}
